    margin-right: 5px;
}

.delete-rule {
    width: 15px;
}

ul.ui-search-menu,
ul.ui-nav-menu {
    list-style-type: none;
}

ul.ui-search-menu li a,
ul.ui-nav-menu li a,
.soptclass,
.clearsearchclass {
    text-decoration: none;
    color: #010101;
}

ul.ui-search-menu li a:hover,
ul.ui-nav-menu li a:hover,
a.soptclass:hover,
a.clearsearchclass:hover {
    background-color: $borderColor;
    padding: 1px 1px;
