}

.column-menu .divider {
    background-color: #e5e5e5;
    height: 1px;
    padding: 0 0;
    margin: 5px 0;
    overflow: hidden;
}

.ui-menu-item .ui-common-table .menu_icon {
    padding-right: 8px;
    padding-left: 8px;
}

td.menu_text {
    width: auto;
    padding-left: 1px;
    white-space: nowrap;
}

#search_menu .ui-menu-item div {
    padding: 3px 0;
    white-space: nowrap;
}

#search_menu .search_buttons {
    display: inline-block;
    width: 50%;
