@at-root {
    @font-face {
        font-family: $iconfontName;
        src: url('../fonts/#{$iconfontFileName}.eot?');
        src: url('../fonts/#{$iconfontFileName}.eot?#iefix') format('embedded-opentype'), url('../fonts/#{$iconfontFileName}.woff?') format('woff'), url('../fonts/#{$iconfontFileName}.ttf?') format('truetype'), url('../fonts/#{$iconfontFileName}.svg?#iconfont') format('svg');
    }
}

[class^="#{$iconfontClassName}"]:before,
[class*=" #{$iconfontClassName}"]:before{
    content:"";
    font-family: $iconfontName;
    font-style: normal;
    font-weight: normal;
    speak: none;
    display: inline-block;
    text-decoration: inherit;
    width: 1em;
    margin-right: .2em;
    text-align: center;
    font-variant: normal;
    text-transform: none;
    line-height: 1em;
    margin-left: .2em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}



