}

.tab-nav:after {
  clear: both;
}

.tab-nav ul {
  padding: 0;
  list-style: none;
  height: 100%;
  width: 9999px;
  border-bottom: 1px solid #dfdfdf;
}

.tab-nav li {
  cursor: pointer;
