  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #999;
  border: 1px solid #dfdfdf;
  border-left: 0;
  float: left;
  background: #f8f8f8;
  position: relative;
  top: 0;
}

.tab-nav li:nth-child(1) {
