}

.tab-nav.tab-ladder ul {
  border-bottom: 0;
}

.tab-nav.tab-ladder li {
  width: 160px;
  border: 0;
  height: 30px;
  line-height: 30px;
  text-align: left;
  padding-top: 0;
  background: transparent;
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
}

.tab-nav.tab-ladder li .icon-remove2 {
  position: absolute;
  right: 5px;
  top: 0;
}

.tab-nav.tab-ladder li:hover {
  color: #999;
}

.tab-nav.tab-ladder li:before {
  content: "";
  border-top: 30px solid #dfdfdf;
  border-left: 7.5px solid transparent;
  border-right: 7.5px solid transparent;
  position: absolute;
  width: 167.5px;
  left: 0px;
  top: 0;
  z-index: -1;
}

.tab-nav.tab-ladder li:after {
  content: "";
  border-top: 29px solid #f5f5f5;
  border-left: 7.5px solid transparent;
  border-right: 7.5px solid transparent;
  position: absolute;
  width: 165.5px;
  left: 1px;
  top: 0;
  z-index: -1;
}

.tab-nav.tab-ladder li.active {
  border-top: 0;
}

.tab-nav.tab-ladder li.active:after {
  border-top-color: #fff;
}

.tab-nav.tab-ladder li.tab-add {
  background: gainsboro;
  margin-left: 15px;
  width: auto;
  color: #fff;
  padding: 0;
}

.tab-nav.tab-ladder li.tab-add:after, .tab-nav.tab-ladder li.tab-add:before {
  width: 0;
  height: 0;
  border: 30px solid transparent;
  border-left-width: 7.5px;
  border-right-width: 7.5px;
}

.tab-nav.tab-ladder li.tab-add:after {
  border-left-color: gainsboro;
  border-top-color: gainsboro;
  right: -7.5px;
  left: auto;
}

.tab-nav.tab-ladder li.tab-add:before {
  border-right-color: gainsboro;
  border-bottom-color: gainsboro;
  left: -14px;
}

.tab-nav.tab-ladder li.tab-nav-btn-prev, .tab-nav.tab-ladder li.tab-nav-btn-next {
  position: absolute;
  background: gainsboro;
  width: 17px;
  text-align: center;
  padding: 0;
}

.tab-nav.tab-ladder li.tab-nav-btn-prev:after, .tab-nav.tab-ladder li.tab-nav-btn-prev:before, .tab-nav.tab-ladder li.tab-nav-btn-next:after, .tab-nav.tab-ladder li.tab-nav-btn-next:before {
  width: 0;
  height: 0;
  border: 30px solid transparent;
  border-left-width: 7.5px;
  border-right-width: 7.5px;
}

.tab-nav.tab-ladder li.tab-nav-btn-prev:after, .tab-nav.tab-ladder li.tab-nav-btn-next:after {
  border-left-color: gainsboro;
  border-bottom-color: gainsboro;
  right: -14px;
  left: auto;
}

.tab-nav.tab-ladder li.tab-nav-btn-prev:before, .tab-nav.tab-ladder li.tab-nav-btn-next:before {
  border-right-color: gainsboro;
  border-bottom-color: gainsboro;
  left: -14px;
}

