.ui-jqgrid .ui-jqgrid-view input,
.ui-jqgrid .ui-jqgrid-view select,
.ui-jqgrid .ui-jqgrid-view textarea,
.ui-jqgrid .ui-jqgrid-view button {
  /*font-size: 11px*/
}

.ui-jqgrid .ui-scroll-popup {
  width: 100px;
}

.ui-search-table select,
.ui-search-table input {
  padding: 4px 3px;
}

.ui-disabled {
  opacity: .35;
  filter: Alpha(Opacity=35);
  /* support: IE8 */
  background-image: none;
}

.ui-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: .3;
  filter: Alpha(Opacity=30);
  /* support: IE8 */
}

.ui-jqgrid-pager .ui-pg-table .ui-pg-button:hover,
.ui-jqgrid-toppager .ui-pg-table .ui-pg-button:hover {
  background-color: #ddd;
}

.ui-jqgrid-corner {
  border-radius: 5px;
}

.ui-resizable-handle {
  position: absolute;
  display: block;
}

.ui-jqdialog .ui-resizable-se {
  width: 12px;
  height: 12px;
  right: 5px;
  bottom: 5px;
  background-position: 16px 16px;
}

.ui-resizable-se {
  cursor: se-resize;
  width: 12px;
  height: 12px;
  right: 1px;
  bottom: 1px;
}

.ui-top-corner {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.ui-bottom-corner {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.ui-search-table {
  margin-bottom: 0;
}

.ui-search-table .columns,
.ui-search-table .operators {
  padding-right: 5px;
}

.opsel {
  float: left;
  width: 100px;
  margin-right: 5px;
}

.add-group,
.add-rule,
.delete-group {
  width: 14%;
  margin-right: 5px;
}

.delete-rule {
  width: 15px;
}

ul.ui-search-menu,
ul.ui-nav-menu {
  list-style-type: none;
}

ul.ui-search-menu li a,
ul.ui-nav-menu li a,
.soptclass,
.clearsearchclass {
  text-decoration: none;
  color: #010101;
}

ul.ui-search-menu li a:hover,
ul.ui-nav-menu li a:hover,
a.soptclass:hover,
a.clearsearchclass:hover {
  background-color: #ddd;
  padding: 1px 1px;
  text-decoration: none;
}

ul.ui-search-menu li,
ul.ui-nav-menu li {
  padding: 5px 5px;
}

.ui-menu-item hr {
  margin-bottom: 0px;
  margin-top: 0px;
}

.searchFilter .ui-search-table td,
.searchFilter .ui-search-table th {
  border-top: 0px none !important;
}

.searchFilter .queryresult {
  margin-bottom: 5px;
}

.searchFilter .queryresult tr td {
  border-top: 0px none;
}

.ui-search-label {
  padding-left: 5px;
}

.frozen-div,
.frozen-bdiv {
  background-color: #fff;
}

/* Column menu */
.ui-jqgrid .ui-jqgrid-htable .colmenu {
  position: absolute;
  right: 1px;
  height: 100%;
  color: black;
}

.ui-jqgrid .ui-jqgrid-htable .colmenu-rtl {
  right: auto;
  left: 1px;
}

.ui-jqgrid .ui-jqgrid-htable .colmenuspan {
  display: inline-block;
}

.ui-jqgrid .ui-jqgrid-htable .ui-th-div {
  height: 17px;
  margin-top: 5px;
}

.column-menu,
.ui-search-menu {
  padding: 10px 15px;
}

.column-menu .divider {
  background-color: #e5e5e5;
  height: 1px;
  padding: 0 0;
  margin: 5px 0;
  overflow: hidden;
}

.ui-menu-item .ui-common-table .menu_icon {
  padding-right: 8px;
  padding-left: 8px;
}

td.menu_text {
  width: auto;
  padding-left: 1px;
  white-space: nowrap;
}

#search_menu .ui-menu-item div {
  padding: 3px 0;
  white-space: nowrap;
}

#search_menu .search_buttons {
  display: inline-block;
  width: 50%;
  padding: 3px 3px;
}

.search_buttons .btn-default:hover {
  padding-bottom: 6px;
  padding-top: 6px;
}

.search_buttons #bs_reset {
  margin-right: 3px;
}

.search_buttons #bs_search {
  margin-left: 3px;
}

.index-sidebar-icon {
  position: absolute;
  left: 10px;
  font-size: 1.2em;
}

.index-panel .up-panel-body {
  height: 230px;
}

.demochart {
  height: 100%;
  width: 100%;
}

.dataSource-data {
  padding-right: 215px;
  height: 100%;
  margin-top: -16px;
}

.dataSource-sidebar {
  width: 215px;
  position: absolute;
  right: 0;
  top: 0;
  background: #f7f9fa;
  height: 100%;
  padding-top: 48px;
}

.dataSource-sidebar .dataSource-sidebar-nav {
  height: 48px;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}

.dataSource-sidebar .dataSource-sidebar-nav li {
  height: 48px;
  line-height: 48px;
  float: left;
  width: 50%;
  background: #e7ebec;
  border: 0;
}

.dataSource-sidebar .dataSource-sidebar-nav li.active {
  background: transparent;
  padding-top: 1px;
}

.dataSource-sidebar .dataSource-sidebar-nav li:hover {
  color: inherit;
}

.dataSource-sidebar > .tab-wrapper {
  height: 100%;
}

.dataSource-sidebar .dataSource-sidebar-list {
  height: 100%;
  padding: 20px 15px;
}

.dataSource-sidebar .dataSource-sidebar-list .up-panel {
  background: #fff;
  border-radius: 3px;
  margin-bottom: 20px;
  box-shadow: 0 0 8px 1px rgba(0, 0, 0, 0.1);
}

.dataSource-sidebar .dataSource-sidebar-list .up-panel .up-panel-title {
  text-align: center;
  width: 100%;
  font-size: 14px;
  padding-top: 10px;
  font-weight: bold;
}

.dataSource-sidebar .dataSource-sidebar-list .up-panel .up-panel-body {
  height: 130px;
  padding: 0;
}

.production-panel .up-panel-body {
  height: 350px;
}
